
import Vue from 'vue';
import Router from 'vue-router';
import NProgress from 'nprogress';
import Meta from 'vue-meta';
import admin from "./admin";
import userPath from "./user";
import { getToken } from '@/utils/auth'
Vue.use(Router);
Vue.use(Meta);

var userInfo = JSON.parse(sessionStorage.getItem("user_obj"));
export var constantRouterMap = [];
if (userInfo && userInfo.mode == 1) {
    constantRouterMap = admin;
} else {
    constantRouterMap = userPath;
}



const router = new Router({
    // mode: 'history',
    routes: constantRouterMap,
    scrollBehavior: () => ({ y: 0 }),
});

router.beforeEach((to, from, next) => {
    // var token = getToken('Token');
    // if (token) {
    //     if (to.path === '/') {
    //         next()
    //         NProgress.done()
    //     } else {
    //         var user = JSON.parse(sessionStorage.getItem("user_obj"));
    //         if (user.mode) {
    //             debugger
    //             console.log(77777, user.mode, admin)
    //             router.addRoutes(admin)
    //             next()
    //         } else {
    //             debugger
    //             console.log(88888, user.mode, userPath)
    //             router.addRoutes(userPath)
    //             next()
    //         }
    //     }
    // } else {
    //     next()
    // }
    next()
});

window.changeRouter = function () {
    var userInfo = JSON.parse(sessionStorage.getItem("user_obj"));
    constantRouterMap = [];
    if (userInfo && userInfo.mode == 1) {
        constantRouterMap = admin;
        router.addRoutes(admin) 
    } else {
        constantRouterMap = userPath;
        router.addRoutes(userPath) 
    }
};

router.afterEach(() => {
    NProgress.done();
});

export const asyncRouterMap = constantRouterMap;
export default router;
