import { Layout } from "../layout"; 
export default [
    {
        path: '/',
        component: () => import('@/page/login'),
        //hidden: true,
    },
    {
        path: '/index',
        component: Layout,
        redirect: '/index/index',
        category: '',
        hidden: false,
        children: [
            {
                path: 'index',
                component: () =>import ('@/page/index/index'),
                name: 'index',
                meta: { icon: 'el-icon-s-home', title: '首页' },
            },
        ],
    },
    {
        path: '/wholeAnalysis',
        component: Layout,
        redirect: '/wholeAnalysis',
        meta: { icon: 'el-icon-s-data', title: '总体分析' },
        children: [
            {
                path: 'a',
                component: () => import ('@/page/wholeAnalysis/a'),
                name: 'a',
                meta: { title: '攻击能力分析' },
            },
            {
                path: 'aTrend',
                component: () => import ('@/page/wholeAnalysis/aTrend'),
                name: 'aTrend',
                meta: { title: '攻击能力变化趋势' },
            },
            {
                path: 'b',
                component: () => import ('@/page/wholeAnalysis/b'),
                name: 'b',
                meta: { title: '生存能力分析' },
            },
            {
                path: 'fTrend',
                component: () => import ('@/page/wholeAnalysis/fTrend'),
                name: 'fTrend',
                meta: { title: '生存能力变化趋势' },
            },
            {
                path: 'violation',
                component: () => import ('@/page/wholeAnalysis/violation'),
                name: 'violation',
                meta: { title: '违规能力数据分析' },
            },
            {
                path: 'theory',
                component: () => import ('@/page/wholeAnalysis/theory'),
                name: 'theory',
                meta: { title: '理论水平' },
            },
        ],
    },
    {
        path: '/dataSearch',
        component: Layout,
        redirect: '/dataSearch/dataSearch',
        category: '',
        hidden: false,
        children: [
            {
                path: 'dataSearch',
                component: () =>import ('@/page/dataSearch/dataSearch'),
                name: 'dataSearch',
                meta: { icon: 'el-icon-s-flag', title: '能力对比' },
            },
        ],
    },
    {
        path: '/dataQuery',
        component: Layout,
        redirect: '/dataQuery/dataQuery',
        category: '',
        hidden: false,
        children: [
            {
                path: 'dataQuery',
                component: () =>import ('@/page/dataAdd/dataQuery'),
                name: 'dataQuery',
                meta: { icon: 'el-icon-search', title: '数据查询' },
            },
        ],
    },
    {
        path: '/dataAdd',
        component: Layout,
        redirect: '/dataAdd/dataAdd',
        category: '',
        hidden: false,
        children: [
            {
                path: 'dataAdd',
                component: () =>import ('@/page/dataAdd/dataAdd'),
                name: 'dataAdd',
                meta: { icon: 'el-icon-s-order', title: '违规数据录入' },
            },
        ],
    },
    {
        path: '/study',
        component: Layout,
        redirect: '/study/study',
        category: '',
        hidden: false,
        children: [
            {
                path: 'study',
                component: () => import ('@/page/userList/study.vue'),
                name: 'study',
                meta: { icon: 'el-icon-s-claim', title: '理论学习' },
            },
        ],
    },
    
];