import { Layout } from "../layout"; 
export default  [
    {
        path: '/',
        component: () => import('@/page/login'),
        //hidden: true,
    },
    {
        path: '/index',
        component: Layout,
        redirect: '/index/index',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'index',
                component: () =>import ('@/page/index/index'),
                name: 'index',
                meta: { icon: 'el-icon-s-home', title: '首页' },
            },
        ],
    },
    {
        path: '/wholeAnalysis',
        component: Layout,
        redirect: '/wholeAnalysis',
        meta: { 
            icon: 'el-icon-s-data', 
            title: '总体分析',
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'a',
                component: () => import ('@/page/wholeAnalysis/a'),
                name: 'a',
                meta: { title: '攻击能力分析' },
            },
            {
                path: 'aTrend',
                component: () => import ('@/page/wholeAnalysis/aTrend'),
                name: 'aTrend',
                meta: { title: '攻击能力变化趋势' },
            },
            {
                path: 'b',
                component: () => import ('@/page/wholeAnalysis/b'),
                name: 'b',
                meta: { title: '生存能力分析' },
            },
            {
                path: 'fTrend',
                component: () => import ('@/page/wholeAnalysis/fTrend'),
                name: 'fTrend',
                meta: { title: '生存能力变化趋势' },
            },
            {
                path: 'violation',
                component: () => import ('@/page/wholeAnalysis/violation'),
                name: 'violation',
                meta: { title: '违规能力数据分析' },
            },
            {
                path: 'theory',
                component: () => import ('@/page/wholeAnalysis/theory'),
                name: 'theory',
                meta: { title: '理论水平' },
            },
        ],
    },
    {
        path: '/dataSearch',
        component: Layout,
        redirect: '/dataSearch/dataSearch',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'dataSearch',
                component: () =>import ('@/page/dataSearch/dataSearch'),
                name: 'dataSearch',
                meta: { icon: 'el-icon-s-flag', title: '能力对比' },
            },
        ],
    },
    {
        path: '/dataQuery',
        component: Layout,
        redirect: '/dataQuery/dataQuery',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'dataQuery',
                component: () =>import ('@/page/dataAdd/dataQuery'),
                name: 'dataQuery',
                meta: { icon: 'el-icon-search', title: '数据查询' },
            },
        ],
    },
    {
        path: '/dataAdd',
        component: Layout,
        redirect: '/dataAdd/dataAdd',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'dataAdd',
                component: () =>import ('@/page/dataAdd/dataAdd'),
                name: 'dataAdd',
                meta: { icon: 'el-icon-edit-outline', title: '违规数据录入' },
            },
        ],
    },
    {
        path: '/dataAddAdmin',
        component: Layout,
        redirect: '/dataAdd/dataAddAdmin',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin'],
        },
        children: [
            {
                path: 'dataAddAdmin',
                component: () =>import ('@/page/dataAdd/dataAddAdmin'),
                name: 'dataAdd',
                meta: { icon: 'el-icon-menu', title: '违规管理' },
            },
        ],
    },
    {
        path: '/study',
        component: Layout,
        redirect: '/study/study',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'study',
                component: () => import ('@/page/userList/study.vue'),
                name: 'study',
                meta: { icon: 'el-icon-s-claim', title: '理论学习' },
            },
        ],
    },
    {
        path: '/adminAddData',
        component: Layout,
        redirect: '/adminAddData/adminAddData',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin', 'editor'],
        },
        children: [
            {
                path: 'adminAddData',
                component: () => import ('@/page/dataAdd/adminAddData'),
                name: 'adminAddData',
                meta: { icon: 'el-icon-s-order', title: '批次管理' },
            },
        ],
    },
    {
        path: '/userList',
        component: Layout,
        redirect: '/userList/userList',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin'],
        },
        children: [
            {
                path: 'userList',
                component: () => import ('@/page/userList/userList'),
                name: 'userList',
                meta: { icon: 'el-icon-user-solid', title: '人员管理' },
            },
        ],
    },
    {
        path: '/plane',
        component: Layout,
        redirect: '/plane/plane',
        category: '',
        hidden: false,
        meta:{
            roles: ['admin'],
        },
        children: [
            {
                path: 'plane',
                component: () => import ('@/page/dataAdd/plane'),
                name: 'plane',
                meta: { icon: 'el-icon-s-promotion', title: '飞机管理' },
            },
        ],
    },
];