<template>
  <header class="head-nav rflex" id="header_container">
    <div class="right-nav" ref="rightNav">
      <!-- <top-menu></top-menu> -->
      <div class="userinfo-right rflex">
        <div class="userinfo">
          <el-menu class="el-menu-demo" mode="horizontal">
            <el-submenu index="2" popper-class="infoItem">
              <template slot="title">
                <div class="welcome">
                  <span class="name">你好,</span>
                  <span class="name avatarname">{{ userName }}</span>
                </div>
              </template>
              <el-menu-item index="2-3" @click="setDialogInfo('logout')">退出！</el-menu-item>
              <el-menu-item index="2-3" @click="setDialogInfo('pass')">修改密码</el-menu-item>
            </el-submenu>
            <span class="off">{{type}}</span>
          </el-menu>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import * as mUtils from "@/utils/mUtils";
import { setToken, getToken } from "@/utils/auth";
// import topMenu from "./topMenu";
import wechatImg from "@/assets/img/wechat.jpg";
import qqImg from "@/assets/img/qq.png";
import logoImg from "@/assets/img/logo.png";
import chinaImg from "@/assets/img/china.svg";
import americaImg from "@/assets/img/america.svg";
import { github } from "@/utils/env";
import { getUserList, add, edit } from "@/api/user";
import md5 from "js-md5";
export default {
  name: "head-nav",
  data() {
    return {
      logo: logoImg,
      langLogo: getToken("langLogo") || americaImg,
      chinaImg: chinaImg,
      americaImg: americaImg,
      wechat: {
        wechatImg: wechatImg,
        isWechat: false
      },
      userName: "admin",
      qq: {
        qqImg: qqImg,
        isQq: false
      },
      type: "离线",
      //headNavWidth:0,
      github: github,
      menu: {
        userBgcolor: "#f0f2f5"
      }
    };
  },
  components: {
    //topMenu
  },
  computed: {
    headNavWidth() {
      return document.body.clientWidth - 0 - 150;
    }
  },
  created() {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      var user = this.sessionData("get", "user");
      var type = this.sessionData("get", "serverUrl");
      if (user) {
        this.userName = user.name;
      }
      if (type) {
        this.type = type == "http://127.0.0.1:8088" ? "离线" : "在线";
      }
    },
    showWechat() {
      this.wechat.isWechat = true;
    },
    hideWechat() {
      this.wechat.isWechat = false;
    },
    showQq() {
      this.qq.isQq = true;
    },
    hideQq() {
      this.qq.isQq = false;
    },
    logout() {
      // this.$store.dispatch("LogOut").then(() => {
      //   location.reload();
      // });
      window.location.href = window.location.origin + "/dist/index.html";
    },
    /**
     * 弹出框-修改密码或者系统设置
     * @param {string} cmditem 弹框类型
     */
    setDialogInfo(cmditem) {
      switch (cmditem) {
        case "info":
          this.$router.push("/infoManage/infoShow/infoShow1");
          break;
        case "pass":
          //this.$router.push("/infoManage/infoModify/infoModify1");
          //this.$router.push("/userList/userList");
          this.open();
          break;
        case "logout":
          this.logout();
          break;
      }
    },
    open() {
      this.$prompt("", "请输入密码").then(({ value }) => {
        var user = this.sessionData("get", "user");
        var del = {
          _id: user._id,
          data: {
            passwd: md5(value)
          }
        };
        edit(del).then(res => {
          if (res.code == 0) {
            this.$message({
              message: "修改密码成功！",
              type: "success"
            });
          }
        });
      });
    },
    // 切换语言
    changeLocale(type) {
      setToken("lang", type);
      this.$i18n.locale = type;
      if (type === "en") {
        this.langLogo = this.americaImg;
      } else {
        this.langLogo = this.chinaImg;
      }
      setToken("langLogo", this.langLogo);
    }
  }
};
</script>

<style scoped lang='less'>
.el-message-box {
  background: #063348 !important;
}
.off {
  color: rgb(169, 216, 110);
  margin-left: 70px;
  font-weight: bold;
}
.right-nav {
  display: flex;
  flex: 1;
  width: calc(100% - 180px);
  padding-right: 15px;
  justify-content: space-between;
}
.head-nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 29;
  transition: width 0.2s;
  justify-content: space-between;
  height: 60px;
  box-sizing: border-box;
  .logout {
    vertical-align: middle;
    cursor: pointer;
  }
}
.middle {
  align-items: center;
  border: 1px solid;
}
.userinfo-right {
  width: 320px;
  padding: 0 10px;
  justify-content: space-between;
}
.userinfo {
  line-height: 60px;
  text-align: right;
}
.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  vertical-align: middle;
  display: inline-block;
}
.langAvatar {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  vertical-align: middle;
  display: inline-block;
}
.welcome {
  display: inline-block;
  vertical-align: middle;
  padding: 0 5px;
  margin-left: 86px;
  .name {
    line-height: 20px;
    text-align: center;
    font-size: 12px;
  }
  .avatarname {
    color: #a9d86e;
    font-weight: bolder;
    font-size: 13px;
  }
}

.username {
  cursor: pointer;
  margin-right: 5px;
}
.border {
  border: 1px solid;
}
.notify-row {
  line-height: 60px;
  flex: 1;
  ul {
    display: flex;
    justify-content: space-around;
  }
}

ul.top-menu > li {
  position: relative;
}
</style>
