import '@/assets/remark/base/css/site.css';
import '@/assets/remark/global/css/bootstrap-extend.css';
import '@/assets/remark/global/fonts/web-icons/web-icons.css';

import Vue from 'vue'
import App from './App.vue'
import router from './router/index.js'
import ElementUI from 'element-ui'
import tool from "./utils/tool"
import 'element-ui/lib/theme-chalk/index.css'
import echarts from 'echarts'
import { localData, sessionData } from "./utils/local"
import { Loading } from 'element-ui'
import './components/iconSvg' // iconSvg

//import store from './store/'
//import '@/permission' // permission control


Vue.prototype.localData = localData;
Vue.prototype.sessionData = sessionData;
Vue.config.productionTip = false;
Vue.prototype.$tool = tool;
Vue.use(ElementUI);
Vue.prototype.$echarts = echarts;
Vue.prototype.$unfly = function (data) {
    data = window.atob(data.replace("FLY/gz", ""));
    data = data.split("").map(function (x) {
        return x.charCodeAt(0);
    });
    data = new Uint8Array(data);
    data = JSON.parse(pako.inflate(data, { to: "string" }));
    return data;
}
Vue.prototype.openLoading = function() {
    const loading = this.$loading({           // 声明一个loading对象
      lock: true,                             // 是否锁屏
      text: '正在加载...',                     // 加载动画的文字
      spinner: 'el-icon-loading',             // 引入的loading图标
      background: 'rgba(0, 0, 0, 0.3)',       // 背景颜色
      target: '.sub-main',                    // 需要遮罩的区域
      body: true,                              
      customClass: 'mask'                     // 遮罩层新增类名
    })
    return loading;
  }

new Vue({
    router,
    //store,
    render: h => h(App)
}).$mount('#app')