import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import { getToken } from '@/utils/auth'
import md5 from 'js-md5';
// 创建axios实例
let service =  axios.create({
  baseURL: window.baseURL || "http://127.0.0.1:8088",
  timeout: 10000 // 请求超时时间
})
//service.defaults.headers.post['Content-Type'] = 'multipart/form-data';
service.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
// request拦截器
service.interceptors.request.use(config => {
  config.baseURL = window.baseURL || sessionStorage.getItem("serverUrl_str");
  if(config.url == "/user/login"){
    config.params.passwd = md5(config.params.passwd);
  }else{
    if(!config.params){
      config.params = {};
    }
    if(config.url != "/user/add"){
      config.params.token = getToken('Token');
    }

    // if(config.url == "//add"){
    //   //config.headers['Content-Type'] =  'multipart/form-data';
    //   config.params.append("token", getToken('Token'));
    // }
  }
  return config
}, error => {
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code !== 0) {
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })
      // 根据服务端约定的状态码：5001:非法的token; 5002:其他客户端登录了; 5004:Token 过期了;
      if (res.code === 65280 || res.code === 5002 || res.code === 5004) {
          MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
            confirmButtonText: '重新登录',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            window.location.href = window.location.origin + "/#/";
          })
      }
      return Promise.reject('error')
    } else {
        return response.data
    }
  },
  error => {
    // Message({
    //   message: error.message,
    //   type: 'error',
    //   duration: 5 * 1000
    // })
    console.log("接口报错",error.message)
    return Promise.reject(error)
  }
)

export default service
